                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t("Reject") }}
                        </h5>
                        <button v-on:click="ClosePOPuP($event)" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                    </div>
                    <div class="border p-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"  v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
     
