</template>

<script>
import DataService from '../../service/DataService'
export default{
   props:{
    RejectData:{
        type:Array,
        required:true,
    }
   },
    data(){
        return{
            FormSchema: [
                        {
                            layoutType: "single",
                            Data: [
                                {
                                    astype: "TextAreaField",
                                    label: this.$t('Reason'),
                                    name: "Reason",
                                    value: "",
                                    validationRules: { "required": true, max: 1000 },
                                    placeholder: ""  ,
                                    config: {
                                        rows:0
                                    }
                                },
                            ]
                        }
                          
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
            ],                    
            assignmentId:'',            
            CancelledBy:'ASSIGNER',
            RejectsData:[],
            isLoading:false,
        }
    },
    created:function(){                
    },
    methods:{

        onSubmit:function(formfields){
            debugger;           
            var vm=this; 
            vm.isLoading=true; 
            debugger;
            const RequestionData ={
                    batchId:vm.RejectData.REQUISITION_BATCH_ID,
                    assignmentId  :vm.assignmentId,
                    requisitionCancelledFrom:vm.CancelledBy,
                    comment:formfields.Reason
                }                             
                 DataService.UpdateRequisitionStatus(RequestionData).then(response=>{                                                   
                     if(response.data!=null){
                         if(response.data.length>0){                              
                             if(response.data==1)
                             {                                   
                                 vm.ShowAlert(vm.$t("Record Reject Successfully"), "success",vm.$t('Alert'));
                                 vm.isLoading=false; 
                                 vm.$parent.FetchData();
                                 vm.$parent.ClosePOPuP();
                             }
                             else{
                                 vm.ShowAlert(vm.$t("Unknown Error Occur"), "failure", true, vm.$t('Alert'));
                                 vm.isLoading=false; 
                                 vm.$parent.ClosePOPuP();
                             }
                         }
                         else{
                             vm.ShowAlert(vm.$t("Unknown Error Occur"), "failure", true, vm.$t('Alert'));
                             vm.isLoading=false; 
                             vm.$parent.ClosePOPuP();
                         }
                     }
                     setTimeout(function(){vm.CheckBoxBootstrap()}, 100);   
                 });

        },       
         
        ClosePOPuP:function(item){  
            this.$emit('ClosePOPuP',item);
        },
        onCancel:function(){
            debugger
            var vm = this;
            vm.FormSchema[0].Data[0].value=''
            vm.ClosePOPuP();
        }        
    }
}
</script>